





















































import { Component, Vue } from 'vue-property-decorator';

@Component
export default class JoinForm extends Vue{

  email: string;
  pw1: string;
  pw2: string;
  name: string;
  phone: string;

  alertEmail: string;
  alertPw1: string;
  alertPw2: string;

  constructor() {
    super();
    this.email = '';
    this.pw1 = '';
    this.pw2 = '';
    this.name = '';
    this.phone = '';

    this.alertEmail = '';
    this.alertPw1 = '';
    this.alertPw2 = '';
  }

  async join(){
    if(confirm('가입하시겠습니까?')){
      const { result } = await Vue.axios.post('/user/signUp',{
        email: this.email,
        password: this.pw1,
        name: this.name,
        phone: this.phone,
      }) as { result: boolean };

      if(result){
        await this.$router.push({
          path: '/login',
        })
      } else {
        alert('가입 실패...');
        throw new Error();
      }
    }
    await this.home();
  }

  //----   유효성 검사   -----

  //이메일
  async emailCheck(){
    const regExp = /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/i;
    if(regExp.test(this.email)){
      const { result } = await Vue.axios.post('/user/emailCheck',{email: this.email}) as { result: boolean};

      if(result){
        this.alertEmail = '사용가능한 이메일 입니다.';
      } else {
        this.alertEmail = '중복된 이메일 입니다.';
      }
    } else {
      this.alertEmail = '이메일 형식이 맞지 않습니다.';
      this.email = '';
      //this.$refs.email.focus();
    }
  }

  //비밀번호
  pwCheck1(){
    let pw1 = this.pw1;
    let regExp = /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#?&])[A-Za-z\d@$!%*#?&]{8,}$/;
    if(!regExp.test(pw1)){
      this.alertPw1 = '하나 이상의 문자 및 특수문자가 포함되어야합니다.';
      this.pw1 = '';
    } else {
      this.alertPw1 = '사용가능한 비밀번호 입니다.';
    }
  }

  pwCheck2() {
    let pw1 = this.pw1;
    let pw2 = this.pw2;

    if(pw1 !== pw2){
      this.alertPw2 = '비밀번호가 일치하지 않습니다.';
    } else {
      this.alertPw2 = '비밀번호가 일치합니다.';
    }
  }



  //핸드폰
  async phoneCheck(){
    const regExp = /^01([0|1|6|7|8|9])-?([0-9]{3,4})-?([0-9]{4})$/;
    if(!regExp.test(this.phone)){
      alert('전화번호 확인');
      this.phone = '';
    }
  }

  async home(){
    await this.$router.push({
      path: '/'
    })
  }
}

